import React from 'react';
import './UsuarioGithub.css'

const UsuarioGithub = (props) => {
    const datoUsuario = props.datoGithub;
    // console.log(datoUsuario);
    return (
        <div className="card text-white bg-primary mb-3 mt-3 ms-3 me-3" >
        <div className="card-header" key={datoUsuario.id}><h2>{datoUsuario.login}</h2></div>
        <div className="card-body">
            <h4 className="card-title">Se busca</h4>
            <img src={datoUsuario.avatar_url} alt="Avatar de usuario" />
            <p className="card-text">URL: <a href={datoUsuario.html_url} target="_blank" >web del hacker</a></p>
            <h6>id: {datoUsuario.id}</h6>
        </div>
        </div>
    )
}

export default UsuarioGithub
