import React from 'react';
import UsuarioGithub from './UsuarioGithub.jsx';

const MuestraUsuarios = (props) => {
   const listaUsuarios = props.listaUsuarios;
   console.log(listaUsuarios);
    return (
        <div className="container bg-warning">
            <h2>Los hackers más buscados</h2>
            <h4>Atención!! Use con mucho cuidado esta información, nunca se sabe cuando le pueden estar observando... toc toc.</h4>
            <div className="contiene-tarjetas d-flex flex-wrap justify-content-around align-content-center p-2 bd-highlight">
            {listaUsuarios.map((e,index) =>{
                if (index < 6) {
                    return <UsuarioGithub key={e.id} datoGithub={e}/>
                }
            })}
            </div>
        </div>
    )
}

export default MuestraUsuarios
