import React, { useState, useEffect } from 'react';
import './App.css';
import MuestraUsuarios from './components/MuestraUsuarios.jsx';

function App() {
//import {useEffect} from 'react';
//useEffect(() => {....}, [dependecias])
  const url = 'https://api.github.com/users';
  const [nuevoResultado, setResultado] = useState([])

 
    const recuperaDatos =  async () => {
      try {
        let respuesta = await fetch(url);
        let resultado = await respuesta.json();
        setResultado(resultado);
        return resultado;
      } catch (error) {
        console.log(error)
      }
    }
  useEffect(() => {
    recuperaDatos();
  }, [])
  

  return (
    <div className="App">
      <MuestraUsuarios key={nuevoResultado.id} listaUsuarios={nuevoResultado} />
    </div>
  );
}

export default App;
